/* header styles */
.app-header {
  padding: 10px;
  width: 100%;
  top: 0;
  z-index: 11;
  background: #355c7d; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#c06c84),
    color-stop(#6c5b7b),
    to(#355c7d)
  );
  background: linear-gradient(
    to right,
    #c06c84,
    #6c5b7b,
    #355c7d
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.app-header__logout{
  float: right;
}
.app-footer {
  padding: 10px;
  margin-top: 30px;
  width: 100%;
  position: fixed;
  text-align: center;
  bottom: 0;
  background: #c06c84; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(#355c7d),
    color-stop(#6c5b7b),
    to(#c06c84)
  );
  background: linear-gradient(
    to right,
    #355c7d,
    #6c5b7b,
    #c06c84
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
a#footer-link {
  color: #d3cce3;
  text-decoration: none;
}

.search-details {
  text-transform: capitalize;
  font-size: 12px;
}
* {
  outline: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

header {
  padding: 20px;
  color: white;
}

.logo {
  -webkit-animation: App-logo-spin infinite 10s linear;
          animation: App-logo-spin infinite 10s linear;
  height: 25px;
}

main {
  padding: 0 20px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

table tr td {
  word-break: break-all;
}

.app {
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
}

